import React, { useEffect, useState } from "react";
import { getNcovDataAction } from "../redux/actions";
import { chart_Data } from "../helper/helper";
import store from "../redux/store";
import { Chart, ArcElement } from "chart.js";
import { Doughnut } from "react-chartjs-2";
import List from "./List";
import moment from "moment";
Chart.register(ArcElement);

export default function Graph() {
  var riskarea = [];
  var midarea = [];
  var szMidarea = [];
  var [total, setTotal] = useState(0);
  var [graphData, setGraph] = useState();
  var [listData, setList] = useState();
  var [nsData, setNs] = useState();
  var [ftData, setFt] = useState();
  var [lhuData, setLhu] = useState();
  var [lgData, setLg] = useState();
  var [ytData, setYt] = useState();
  var [lhData, setLh] = useState();
  var [psData, setPs] = useState();
  var [gmData, setGm] = useState();

  useEffect(() => {
    var time = localStorage.getItem("time");
    var diff = 25;
    if (time) diff = timeDiff(time);
    // console.log("time: " + time);
    console.log("diff: " + diff);
    if (diff >= 12) {
      localStorage.setItem("time", moment(new Date()));
      const validate = store.dispatch(getNcovDataAction());
      validate
        .then((data) => {
          // eslint-disable-next-line
          riskarea = store.getState().newslist[0].riskarea;
          // eslint-disable-next-line
          midarea = riskarea.mid;
          // eslint-disable-next-line
          szMidarea = midarea.filter((area) => area.includes(""));
          localStorage.setItem("area", JSON.stringify(szMidarea));
          setArea();
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      szMidarea = JSON.parse(localStorage.getItem("area"));
      setArea();
    }
  }, []);

  function timeDiff(startDate) {
    var endDate = moment(new Date());
    startDate = moment(new Date(startDate));
    var totalMinute = endDate.diff(startDate) / (1000 * 60);
    return Math.floor(totalMinute / 60);
    // return totalMinute;
  }

  function setArea() {
    if (szMidarea) setTotal(szMidarea.length);
    setGraph(<Doughnut {...chart_Data(szMidarea)}></Doughnut>);
    setList(
      <List
        data={szMidarea.filter((area) => area.includes(""))}
        color={0}
        areaName={""}
      ></List>
    );
    setNs(
      <List
        data={szMidarea.filter((area) => area.includes(""))}
        color={1}
        areaName={""}
      ></List>
    );
    setFt(
      <List
        data={szMidarea.filter((area) => area.includes(""))}
        color={2}
        areaName={""}
      ></List>
    );
    setLhu(
      <List
        data={szMidarea.filter((area) => area.includes(""))}
        color={3}
        areaName={""}
      ></List>
    );
    setLg(
      <List
        data={szMidarea.filter((area) => area.includes(""))}
        color={4}
        areaName={""}
      ></List>
    );
    setYt(
      <List
        data={szMidarea.filter((area) => area.includes(""))}
        color={5}
        areaName={""}
      ></List>
    );
    setLh(
      <List
        data={szMidarea.filter((area) => area.includes(""))}
        color={6}
        areaName={""}
      ></List>
    );
    setPs(
      <List
        data={szMidarea.filter((area) => area.includes(""))}
        color={7}
        areaName={""}
      ></List>
    );
    setGm(
      <List
        data={szMidarea.filter((area) => area.includes(""))}
        color={8}
        areaName={""}
      ></List>
    );
  }

  return (
    <div className="chart-box">
      <div className="chart relative">
        {graphData}
        <h3 className="font-bold title">
          Total
          <span className="block text-3xl text-emerald-400">{total}</span>
        </h3>
      </div>
      <div className="list">
        <div className="area-list">{listData}</div>
        <div className="area-list">{nsData}</div>
        <div className="area-list">{ftData}</div>
        <div className="area-list">{lhuData}</div>
        <div className="area-list">{lgData}</div>
        <div className="area-list">{ytData}</div>
        <div className="area-list">{lhData}</div>
        <div className="area-list">{psData}</div>
        <div className="area-list">{gmData}</div>
      </div>
    </div>
  );
}
