import * as actionType from "./types";
import { getNcovData } from "../services/ncov.service";

export const getNcovDataAction = () => (dispatch) => {
  return getNcovData()
    .then((data) => {
      // ! store
      dispatch({
        type: actionType.GETNCOVDATA,
        payload: data,
      });
      return Promise.resolve(data);
    })
    .catch((error) => {
      return Promise.reject(error);
    });
};
